* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif;
    font-weight: 300;
    font-size: .95rem;
}

ul, li {
    list-style-position: inside;
}

h3 {
    font-weight: 600;
    text-align: center;
    font-size: 1.5rem;
}

button {
    border: none;
    background: #265f82;
    color: white;
    cursor: pointer;
    padding: 10px;
    -webkit-transition: background .3s ease;
    -o-transition: background .3s ease;
    transition: background .3s ease;
}

button:hover {
    border: none;
    background: #1F4C69;
    color: white;
    padding: 10px;
}

button:disabled {
    cursor: default;
    background: #698ea5;
}

.centered-form {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    height: 100vh;
    width: 100vw;
    -ms-flex-pack: center;
        justify-content: center; /* ff3.6+ */
    background: -webkit-gradient(linear, left top, right bottom, color-stop(0%, rgba(49,84,129,1)), color-stop(100%, rgba(39,107,130,1))); /* safari4+,chrome */
    background: -webkit-linear-gradient(125deg, rgba(39,107,130,1) 0%, rgba(49,84,129,1) 100%); /* safari5.1+,chrome10+ */
    background: -o-linear-gradient(125deg, rgba(39,107,130,1) 0%, rgba(49,84,129,1) 100%); /* opera 11.10+ */ /* ie10+ */
    background: linear-gradient(325deg, rgba(39,107,130,1) 0%, rgba(49,84,129,1) 100%); /* w3c */
}

.centered-form__form {
    background: rgba(250, 250, 250, 0.9);
    border: 1px solid #e1e1e1;
    border-radius: 5px;
    padding: 0px 20px;
    margin: 20px;
    width: 230px;
}

.form-field {
    margin: 20px 0;
}

.form-field > * {
    width: 100%;
}

.form-field label {
    display: block;
    margin-bottom: 7px;
}

.form-field input, .form-field select {
    border: 1px solid #e1e1e1;
    padding: 10px;
}

.chat {
    display: -ms-flexbox;
    display: flex;
}

.chat__sidebar {
    overflow-y: scroll;
    width: 260px;
    height: 100vh; /* ff3.6+ */
    background: -webkit-gradient(linear, left top, right bottom, color-stop(0%, rgba(49,84,129,1)), color-stop(100%, rgba(39,107,130,1))); /* safari4+,chrome */
    background: -webkit-linear-gradient(125deg, rgba(39,107,130,1) 0%, rgba(49,84,129,1) 100%); /* safari5.1+,chrome10+ */
    background: -o-linear-gradient(125deg, rgba(39,107,130,1) 0%, rgba(49,84,129,1) 100%); /* opera 11.10+ */ /* ie10+ */
    background: linear-gradient(325deg, rgba(39,107,130,1) 0%, rgba(49,84,129,1) 100%); /* w3c */
}

.chat__sidebar h3 {
    color: #e6eaee;
    margin: 10px 20px;
    text-align: left;
}

.chat__sidebar ul {
    list-style-type: none;
}

.chat__sidebar li {
    background: #e6eaee;
    border: 1px solid #e1e1e1;
    border-radius: 5px;
    padding: 10px;
    margin: 10px;
}

.chat__main {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    height: 100vh;
    width: 100%;
}

.chat__messages {
    -ms-flex-positive: 1;
        flex-grow: 1;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
}

.chat__footer {
    background: #e6eaee;
    display: -ms-flexbox;
    display: flex;
    padding: 10px;
    /*height: 60px;*/
    -ms-flex-negative: 0;
        flex-shrink: 0;
}

.chat__footer form {
    -ms-flex-positive: 1;
        flex-grow: 1;
    display: -ms-flexbox;
    display: flex;
}

.chat__footer form * {
    margin-right: 10px;
}

.chat__footer input {
    border: none;
    padding: 10px;
    -ms-flex-positive: 1;
        flex-grow: 1;
}

.chat__messages {
    list-style-type: none;
    padding: 10px;
}

.message {
    padding: 10px;
}

.message__title {
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 5px;
}

.message__title h4 {
    font-weight: 600;
    margin-right: 10px;
}

.message__title span {
    color: #999;
}

@media (max-width: 600px) {
    * {
        font-size: 1rem;
    }

    .chat__sidebar {
        display: none;
    }

    .chat__footer {
        -ms-flex-direction: column;
            flex-direction: column;
    }

    .chat__footer form {
        margin-bottom: 10px;
    }

    .chat__footer button {
        margin-right: 0px;
    }
}

#room-dropdown {
    margin: 15px auto;
    display: block;
    height: 36px;
    width: 120px;
}


